@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', Arial, sans-serif;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.header {
  background-color: #013D5B;
  min-height: 100px;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}


#icon {
  width: 60px;
  position: absolute;
  top: 20px;
  left: 20px;
}

.info {
  width: 100%;
  margin: auto;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  padding-bottom: 20px;
}

form * {
  font-size: 16px;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  border-width: thin;
  font-family: 'Montserrat', Arial, sans-serif;
}

#input {
  border-color: black;
  border-style: solid;
}

#search {
  padding: 10px;
  color: white;
  background-color: green;
  border-style: solid;
  border-color: darkgreen;
}

.nav {
  font-family: 'Montserrat', Arial, sans-serif;
  padding: 10px;
  margin: 10px 10px 0 10px;
  border-radius: 5px;
  border-style: solid;
  border-width: thin;
}

.nav:enabled {
  background-color: white;
}

.nav:disabled {
  background-color: #eee;
}

.nav:active {
  color: black;
}

#searchHeader {
  padding: 20px 0;
  width: 100%;
  background-color: #e7e7e7;
}

.card {
  width: 50%;
  margin: auto;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-style: solid;
  border-width: 1px 0 0 0;
  padding: 5% 0;
}

.card:first-child {
  border-style: none;
}

.cardImage {
  max-width: 100px;
  vertical-align: middle;
}

.image {
  -webkit-flex: 20% 1;
          flex: 20% 1;
}

.description {
  -webkit-flex: 80% 1;
          flex: 80% 1;
  margin: 0 5%;
}

.title {
  font-size: 20px;
  padding: 10px;
}

.moreInfo {
  background-color: #e7e7e7;
  font-family: 'Montserrat', Arial, monospace;
  padding: 10px;
  border-radius: 5px;
  border-style: solid;
  border-width: thin;
}

.moreInfo:active {
  color: black;
}

/* Prevent scrolling when modal dialogue is open */
.ReactModal__Body--open {
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: white;
  overflow: auto;
  border-radius: 10px;
  padding: 20px;
}

.modalOverlay {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.1);
}

.modalInfo {
  text-align: center;
}

.score {
  font-weight: bold;
}

.great {
  color: green;
}

.good {
  color: limegreen;
}

.ok {
  color: gold;
}

.bad {
  color: orange;
}

.awful {
  color: red;
}

.footer {
  background-color: #013D5B;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 20px;
  color: white;
}

h4 {
  font-weight: normal;
}

a {
  color: white;
}

